.navbar {
    box-sizing: border-box;
    width: 100%;
    height: 10vh;
    background: linear-gradient(-45deg,
            #cbb26a,
            #000,
            #be9e44,
            #1a1a1a);
    background-size: 400% 400%;
    animation: gradient 30s ease infinite;
    display: flex;
    justify-content: space-between;
    padding: 0 50px;
    align-items: center;
}

.navbar-left {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-left img {
    height: 70%;
}

.navbar-right {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-right a {
    color: #ffffff;
    text-decoration: none;
    margin: 20px;
    font-size: 20px;
    transition: color 0.5s ease;
    text-align: center;
}

.navbar-right a:hover {
    color: #1a1a1a;
}

.navbar-right button {
    background: none;
    border: none;
    color: #be9e44;
    cursor: pointer;
    display: none;
}

@media(max-width: 900px) {

    .navbar {
        display: flex;
        justify-content: space-between;
        padding: 0 25px;
    }

    .navbar-right a {
        display: none;
    }

    .navbar-right button {
        display: inline-block;
    }
}