.footer {
    width: 100%;
    box-sizing: border-box;
    height: 100px;
    padding: 0 100px;
    background-color: #121619;
    color: #cbb26a;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

.footer img {
    height: 70%;
}

@media(max-width: 900px) {

    .footer {
        display: flex;
        justify-content: space-between;
        padding: 0 25px;
    }

    .footer img {
        display: none;
    }

}