@font-face {
  font-family: "Corsa Grotesk Bold";
  src: url('../styles/corsa-grotesk-bold.otf');

}

@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@600&display=swap');

.home {
  width: 100%;
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: right;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Corsa Grotesk Bold", sans-serif;
}

.homeContainer {

  width: 75%;
  text-align: center;
  margin: 0 auto;
}

.homeContainer .logo {
    
    cursor: pointer;
    font-weight: 700;
    font-size: 3rem;
    background-image: linear-gradient(
      -100deg,
    #cbb26a,
    #000,
    #be9e44,
    #1a1a1a);
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 15s linear infinite;
}

@keyframes textclip {
    to {
        background-position: 200% center;
    }
}

.homeContainer p {
  font-family: 'Times New Roman', Times, serif;
  padding: 0;
  font-size: 1.5rem;
  font-style: italic;
  color: #1a1a1a;
}

