*{
    margin: 0;
    padding: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.contact {
    box-sizing: border-box;
    width: 100%;
    min-height: 90vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.contact-left {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
}

.contact-right {
    height: 100%;
    display: flex;
    
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    background: rgba(148, 148, 148, 0.719);
    backdrop-filter: blur(10px);
}

.contact-right h2 {
    color: #d8c690;
    font-size: 2rem;
    font-weight: 500;
}

.contact-right form {
    margin: 0;
    padding: 0;
    width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact-right form input {
    background-color: transparent;
    width: 100%;
    height: 30px;
    border: none;
    border-bottom: 1px solid #121619;
    color: #121619;
    text-align: center;
}

.contact-right form textarea {
    text-align: center;
    background-color: transparent;
    margin-top: 15px;
    height: 70px;
    width: 100%;
    border: none;
    border-bottom: 1px solid #121619;
    color: #121619;
    resize: none;
}

.contact-right form input::placeholder,
.contact-right form textarea::placeholder {
    text-align: center;
    color: #ffffff;
}

.contact-right form input:focus,
.contact-right form textarea:focus {
    outline: none;
}

.contact-right form label::placeholder{
    text-align: center;
}

.contact-right form label {
    margin-top: 50px;
    color: #d8c690;
}


.checkbox {
    box-sizing: border-box;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.checkbox .checkbox-left{
    width: 10%;
}

.checkbox .checkbox-right {
    padding-left: 1rem;
    text-align: center;
    width: 90%;
}

.buttons {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.contact-right form button,
.buttons a {
    font-size: 1rem;
    margin: 0 auto;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 40px;
    background-color: rgba(0, 0, 0, 0.8);
    color: #ffffff;
    border: none;
    text-align: center;
    border-radius: 15px;
    cursor: pointer;
}

.contact-right form button:hover,
.buttons a:hover {
    background-color: #d8c690;
    color: #121619;
    transition: background-color 0.5s ease;
}

@media (max-width: 900px) {

    .contact-left {
        display: none;
    }
    .contact-right {
        width: 100vw;
    }

    .contact-right form{
        width: 70vw;
    }
    
}

@media (max-width: 900px) {


    .contact-right form{
        width: 90vw;
    }
    
}
